    background: linear-gradient(to bottom, rgba(0,0,0,.7) 30%, transparent 100%);
    display: -webkit-flex;
    display: flex;
    -webkit-align-items: center;
            align-items: center;
    -webkit-justify-content: center;
            justify-content: center;
}

.landing-btn{
    width: 250px;
    background: rgba(255,255,255,.5);
    height: inherit;
    margin: 0 3px;
    display: -webkit-flex;
    display: flex;
    -webkit-align-items: center;
            align-items: center;
    padding: 10px;
    text-transform: uppercase;
    font-family: 'Montserrat',  sans-serif;
    font-size: 20px;
    box-shadow: 10px 10px 15px rgba(0,0,0,.7);
    text-shadow: 2px 2px 3px rgba(0,0,0,.5);
    cursor: pointer;
}

.landing-btn:hover{
    -webkit-transform: scale(102%);
            transform: scale(102%);
}

